import React, { FC, useState, useEffect } from "react"
import { BsCaretUp, BsCaretDownFill, BsCaretUpFill } from "react-icons/bs"
import { Card, CardHeader, Collapse, CardBody, CardFooter } from "reactstrap"
import { useCookies } from "react-cookie"

export const CollapsibleSection: FC<{
  html: string
  title: string
  cookieName?: string
  initialOpen?: boolean
}> = ({
  html,
  title,
  cookieName = "CollapsibleSection: " + title,
  initialOpen = false,
}) => {
  const [cookies, setCookie] = useCookies([cookieName])
  const [isOpen, _setOpen] = useState(false)

  const setOpen = (open: boolean) => {
    setCookie(cookieName, open ? 1 : 0, { maxAge: 3600 * 24 * 30 })
    _setOpen(open)
  }
  const toggle = () => setOpen(!isOpen)

  const upperDropdownIcon = isOpen ? (
    <BsCaretUp title="Close" />
  ) : (
    <BsCaretDownFill title="Open" />
  )

  useEffect(() => {
    if (cookies[cookieName] == undefined) {
      setOpen(initialOpen)
    } else {
      setOpen(cookies[cookieName] == "1")
    }
  }, [])

  return (
    <Card tag="section" style={{ marginBottom: 10 }}>
      <CardHeader
        onClick={toggle}
        style={{ cursor: "pointer" }}
        aria-expanded={isOpen}
        title={isOpen ? "Click to close" : "Click to open"}
      >
        <h3>
          {upperDropdownIcon} {title}
        </h3>
      </CardHeader>
      <Collapse isOpen={isOpen}>
        <CardBody>
          <div dangerouslySetInnerHTML={{ __html: html }} />
        </CardBody>
        <CardFooter
          className="text-center small"
          style={{ cursor: "pointer" }}
          onClick={toggle}
          title="Click to close"
          aria-expanded={isOpen}
        >
          <BsCaretUpFill title="Click to close" />
        </CardFooter>
      </Collapse>
    </Card>
  )
}
